@media (max-width:768px){
    .ProductCard{
        display: grid;
        grid-template-columns: 4fr 1fr;
    }
    .ProductCard img {
        width: 200px;
        height: 136px;
        border-radius: 8px;
        display: block;
        margin-left: auto;
    }
    .ProductCard .productinfo{
        grid-column: 1;
        grid-row: 1;
    }
}
@media (max-width:568px){
    .ProductCard img {
        width: 70px;
        height: 65px;
        border-radius: 8px;
        display: block;
        margin-left: auto;
        transform: translateY(80%);
    }
}
@media (min-width:769px) and (max-width:956px){

    .ProductCard {
        width: 200px;
        border-top-left-radius: 16px;
        border-top-right-radius: 16px;
    }
    .ProductCard img {
        width: 200px;
        height: 136px;
        border-top-left-radius: 16px;
        border-top-right-radius: 16px;
    }
    .productinfo {
        padding-left: 16px;
        padding-right: 16px;
    }
    .productinfo p {
        color: #495E57;
    }
}

@media (min-width:957px){
    .ProductCard {
        width: 250px;
        background-color: #EDEFEE;
        border-top-left-radius: 16px;
        border-top-right-radius: 16px;
    }
    .ProductCard img {
        width: 250px;
        height: 170px;
        border-top-left-radius: 16px;
        border-top-right-radius: 16px;
    }
    .productinfo {
        padding-left: 16px;
        padding-right: 16px;
    }
    .productinfo p {
        color: #495E57;
    }
}

